<template>
  <div class="container">
    <div class="card" style="width: 18rem;">
      <div class="card-body">
        <h5 class="card-title">
          Choose which furniture you want to navigate to
        </h5>
        <h6 class="card-subtitle mb-2 text-muted">Card subtitle</h6>
        <p class="card-text">
          Click on the button below to be pushed to item detail screen
        </p>
        <div v-for="item in allFurniture" :key="item.id">
          <button class="btn btn-primary" @click="pushToItem(item.id)">{{ item.name }}</button>
        </div>

        
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters(["allFurniture"]),
  },
  methods: {
    pushToItem(id) {
      this.$router.push({
        name: `/furniture-detail/${id}`,
      });
    },
  },
};
</script>

<style></style>
